/* The Modal (background) */
#myModal.modal[data-v-a06ee0da] {
    position: fixed; /* Stay in place */
    z-index: 99999999; /* Sit on top */
    padding-top: 50px; /* Location of the box */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}
/* Modal Content */
#myModal .modal-content[data-v-a06ee0da] {
    position: relative;
    background-color: #fefefe;
    margin: auto;
    padding: 0;
    border: 1px solid #888;
    width: 80%;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
            box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
    -webkit-animation-name: animatetop-data-v-a06ee0da;
    -webkit-animation-duration: 0.4s;
    animation-name: animatetop-data-v-a06ee0da;
    animation-duration: 0.4s;
    max-width: 600px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
/* Add Animation */
@-webkit-keyframes animatetop-data-v-a06ee0da {
from {top:-300px; opacity:0
}
to {top:50%; opacity:1
}
}
@keyframes animatetop-data-v-a06ee0da {
from {top:-300px; opacity:0
}
to {top:50%; opacity:1
}
}
/* The Close Button */
#myModal  .modal-close[data-v-a06ee0da] {
    color: white;
    float: right;
    font-size: 28px;
    font-weight: bold;
}
#myModal .modal-close[data-v-a06ee0da]:hover,
#myModal  .modal-close[data-v-a06ee0da]:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
}
#myModal  .modal-header[data-v-a06ee0da] {
    padding: 2px 16px;
    color: white;
}
#myModal  .modal-body[data-v-a06ee0da] {padding: 20px 16px; color: #000; text-align: center;
}
